.main-navigation {
	display: block;

	font-weight: 400;
	clear: left;

	ul {
		display: none;
		list-style: none;
		margin: 0;
		padding-top: 1em;
		padding-left: 0;

		li {
			margin: 0 1px 1px 0;
		}

			&:hover > ul,
			&:focus > ul {
				left: auto;
			}

		a {
			padding-left: .6em;
			color: white;
			font-weight: 100;
			-webkit-font-smoothing: antialiased;
		}

		ul {
			display: none;
			top: 1.5em;
			z-index: 99999;
			margin-top: 1px;
			margin-right: -1px;
			padding-top: 0;

			ul {
				top: 0;
			}

			li {

				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}

				a {
					padding-left: .8em;

					&:before {
						content: "- ";
					}
				}

				li {

					a {
						padding-left: 1.4em;

					}

				}

			}

			a {
				/*width: 200px;*/
			}

		}


	}

	li {
		position: relative;
	}

	a {
		display: inline-block;
		width: 100%;
		padding: .5em 1em .5em 0;
		text-decoration: none;
		color: $color__text-main;
	}

	a:hover,
	a:focus {
		text-decoration: underline;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
	}

	.menu-item-has-children,
	.page_item_has_children {
		min-width: 218px;
	}
	.menu-item-has-children > a,
	.page_item_has_children > a {
		padding-right: 2em;
	}
}

button.dropdown-toggle {
	position: absolute;
	right: 0;
	font-size: 1em;
	border: none;
	background: inherit;
	color: white;
	line-height: 1.5em;
	padding: .4em 1em .4em .5em;
}

.menu-toggle {
	display: block;
	width: 100%;
	margin: 1.2em 1.2em 0 0;
	padding: .6em .8em;
	font-size: 80%;
	text-transform: uppercase;
	color: white;
	background-color: $color__text-main;
	border: none;
	border-radius: 0;
}

/* Toggle small menu and children on */
.toggled-on ul,
.children.toggled-on {
	display: block;
}

@media screen and (min-width: $query__small) {
	.menu-toggle {
		display: none;
	}

	.main-navigation {

		.menu-item-has-children > a,
		.page_item_has_children > a {
			padding-right: 2em;
		}

		ul {
			display: block;
			display: flex;
			flex-wrap: wrap;

			ul {
				flex-direction: column;
				margin-left: 0;

				li {
					padding-left: 0;

					a {
						width: 218px;
					}
				}
			}


			a {
				padding: .4em 1em;
				border: 1px solid white;

			}

		}

	}

}

@media screen and (min-width: $query__medium) {
	.main-navigation {

		ul {
			justify-content: flex-end;
			padding-top: 0;

			/* If you want dropdowns to open on hover, uncomment this: */
			/*
			li:hover > ul,
				li:focus > ul {
				display: block;
			}
			*/
		}

	}
}